/* Toggle Menu */
#nav-toggle {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 0;
  background-color: #b1993e;
  transition: all 0.2s ease-in-out;
  z-index: 10001;
}

#nav-toggle:hover, #nav-toggle:focus, #nav-toggle:active {
  outline: none !important;
}

#nav-toggle .icon-menu {
  position: absolute;
  top: 0;
  left: 0;
}

#nav-toggle .icon-menu {
  width: 32px;
  height: 32px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

#nav-toggle .icon-menu:before {
  display: none;
}

#nav-toggle .icon-bar {
  display: inline-block;
  height: 4px;
  width: 4px;
  margin: 0 1px;
  border-radius: 100%;
  margin-top: 0;
  background-color: #fff;
  border-radius: 3px;
  transition: all 0.2s ease-in-out;
}

#nav-toggle .icon-bar:nth-child(1),
#nav-toggle .icon-bar:nth-child(3) {
  transform: rotate(0deg);
}

#nav-toggle.close .icon-bar {
  width: 32px;
  height: 3px;
  transition: all 0.2s ease-in-out;
}

#nav-toggle.close .icon-bar:nth-child(2) {
  opacity: 0;
}

#nav-toggle.close .icon-bar:nth-child(1),
#nav-toggle.close .icon-bar:nth-child(3) {
  position: absolute;
  margin: 0;
  top: 15px;
  left: 0;
}

#nav-toggle.close .icon-bar:nth-child(1) {
  transform: rotate(-45deg);
}

#nav-toggle.close .icon-bar:nth-child(3) {
  transform: rotate(45deg);
}

